    padding: 0;
  }
.Fish-page{
    background: #010824;
    color: white;
    
}
@font-face {
    font-family: uni sans;
    src: url(../Media/UniSans.otf);
}
@media (min-width: 425px){
    .Fish-page{
        width: 425px;
        margin-left: auto;
        margin-right: auto;
    }
}
.fishes{
    padding: 0px 20px;
    position: fixed;
    background: #010824;
    width: 100vw;
    z-index: 2;
    font-family: uni sans;
}
.Fish1{
    background: #010824;
    color: white;
    height: 100vh;
}
.siam{
    width: 100%;
}
.info{
    padding: 0px 20px;
}
#contact-seller-btn{
    display: grid;
    margin-left: auto;
    margin-right: auto;
    width: 222px;
}