.birds{
    padding: 0px 20px;
    position: fixed;
    background: #010824;
    width: 100vw;
    z-index: 2;
}
.bird-page{
    background: #010824;
    color: white;
}
@media (min-width: 425px){
    .bird-page{
        width: 425px;
        margin-left: auto;
        margin-right: auto;
    }
    .birds{
        width: 384px;
    }
}