@font-face {
    font-family: uni sans;
    src: url(../Media/UniSans.otf);
}
.category-btn{
    height: 107px;
    background-image: url(./icons/dog.jpg);
    position: relative;
    top: 30px;
}
.cart-items{
    position: relative;
    top: 21px;
}
.cartproductimg img{
    padding-right: 8px;
}
#dog h1{
    color: white;
    font-family: uni sans;
    text-align: center;
    padding: 31px 1px;
}
.category img{
    position: relative;
    top: -4px;
    width: 100%;
     
}
@media (min-width: 521px){
    .MuiPaper-root.MuiPaper-elevation2.MuiPaper-rounded{
        width: 352px;
    }
    .category img {
        position: relative;
        top: 186px;
        width: 50%;
    }  
    .items{
        position: relative;
        top: 183px;
    }

}
@media (min-width: 1091px){
    .line1, .line2{
        display: flex;
    }
}