*{
    margin: 0;
    padding: 0;
  }
@font-face {
    font-family: uni sans;
    src: url(./Media/UniSans.otf);
}
@font-face {
    font-family: proxima;
    src: url(./Media/proxima.otf);
}
.home-page{
    background: black;
    height: 120vh;
}
#link{
    display:flex;
    justify-content: space-around;
    color: white;
    background-color: black;
    padding: 24px 0px;
}
.img-home img{
    width: 100%
}
.opt-home h1{
    font-family: uni sans;
    color: white;
    font-size: 250%;
    text-align: center;
    padding: 45px 0px;
}
.description{
    color: white;
    padding: 0px 36px;
    font-family: poxima;
}
.MuiButtonBase-root.MuiButton-root.MuiButton-outlined{
    color: white;
    border: 4px solid white;
    border-radius: 0px;
    font-weight: 900;
}
#btns{
    display: flex;
    justify-content: center;
}
@media (min-width: 777px){
    .img-home img {
        width: 160%;
    }
    #homepageimg{
        content:url(https://i.pinimg.com/564x/43/4e/f8/434ef855bf4775182ab47914b91bceca.jpg)
    }
    .home-page{
        display: flex;
    }
    .opt-home{
        padding-left: 18%;
    }
}
li {
    color: black;
    font-size: 94%;
    font-weight: bold;
    text-align: justify;
}