.slider {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    width: 96.5%;
    border-radius: 10px;
  }
  
  .right-arrow {
    position: absolute;
    top: 50%;
    z-index: 1;
    right: 32px;
    font-size: 3rem;
    color: #fff;
    cursor: pointer;
    user-select: none;
  }
  
  .left-arrow {
    position: absolute;
    top: 50%;
    z-index: 1;
    left: 32px;
    font-size: 3rem;
    color: #fff;
    cursor: pointer;
    user-select: none;
  }
  
  .slide {
    opacity: 0;
    transition-duration: 1s ease;
    height: 40vh;
  }
  
  .slide.active {
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.08);
  }
  @media (min-width:600px){
    .image{
      width: 600px;
    }
  }
  