.logo{
    background: black;
    height: 105px;
}
.logo-img{
    width: 218px;
    margin-left: auto;
    margin-right: auto;
    display: grid;
}
.MuiToolbar-root.MuiToolbar-regular.MuiToolbar-gutters{
    background: #12232e;
}
#brand-name{
    font-family: proxima;
}
#cart-container{
    visibility:hidden;
    position: absolute;
    right: 0;
    z-index: 1;
}
.checkout-btn{
    margin-top: auto;
    padding: 17px;
}
.btns-cart{
    display: flex;
    justify-content: space-between;
}
#back{
    width: 180px;
    height: 42px;
    color: white;
    display: flex;
    position: absolute;
    left: 2%;
    top: 6%;
}
#policy-anc{
    text-decoration: inherit;
    color: white;
}
#policy-anc:hover{
    color: rgb(250, 204, 0);
}
li{
    color: white;
    font-size: 131%;
    font-weight: bold;
}